    width: 25vw;
}

.jumbotron-card-container {    
    background-color: rgba(0,0,0, .1);
    height: 100%;
    border-radius: 0;
}

.jumbotron-card-deck {
    background-color: rgba(255,255,255, .3);
}

.jumbotron-header-subtitle {
    font-size: 2rem;
    color: rgba(0,0,0, .7);
