}

.jumbotron-header-title {
    font-size: 3rem;
    font-weight: 700;
    color: rgb(0,109,165);
    text-shadow: 0 2px 5px rgba(255,255,255,.18);
}

.jumbotron-card-title {
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
}

.jumbotron-card-subtitle {
    font-size: 1.3rem;
    font-weight: 400;
    text-transform: uppercase;
}

.jumbotron-right-container {
    background-color: rgb(255,255,255);
    width: 25vw;
    height: 40vh;
}

.jumbotron-form-money {
    padding: 2vh 5vw;
}

.jumbotron-form-money h3 {
    color: rgb(0,109,165);
    font-weight: 800;
    font-size: 2.5rem;
    padding: 2vh 0;
}

.form-range-slider {    
    width: 40vw;
    -webkit-appearance: none;
}

.form-range-slider:focus {
    outline: none;
}

/* input range in Webkit browsers */
  
.form-range-slider::-webkit-slider-thumb {
    border: 0px solid rgb(255,255,255);
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    background: radial-gradient(rgb(255,255,255), rgb(0,109,165));
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -.75rem;
}

.form-range-slider::-webkit-slider-runnable-track {
    width: 100%;
    height: .5rem;
    cursor: pointer;
    box-shadow: none;
    background: rgb(0,109,165);
    border-radius: 3px;
    border: 0px solid rgb(0,0,0);
}

/* input range in Firefox */

.form-range-slider::-moz-range-track {
    width: 100%;
    height: .5rem;
    cursor: pointer;
    box-shadow: none;
    background: rgb(0,109,165);
    border-radius: 3px;
    border: 0px solid #010101;
}

.form-range-slider::-moz-range-thumb {
    box-shadow: none;
    border: 0px solid rgb(255,255,255);
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    background: radial-gradient(rgb(255,255,255), rgb(0,109,165));
    cursor: pointer;
    margin-top: -.75rem;
}

.form-range-slider::-moz-focus-outer {
    border: 0;
}

/* input range in MS browser */

.form-range-slider::-ms-track {
    width: 100%;
    height: .5rem;
    cursor: pointer;
    box-shadow: none;
    background: transparent;
    border-color: transparent;
    border-width: 1rem 0;
}

.form-range-slider::-ms-thumb {
    border: 0px solid rgb(255,255,255);
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    background: radial-gradient(rgb(255,255,255), rgb(0,109,165));
    cursor: pointer;
    margin-top: 0;
}

.form-range-slider::-ms-fill-lower {
    background:  rgb(0,109,165);
    border-radius: 3px;
}

.form-range-slider::-ms-fill-upper {
    background:  rgb(0,109,165);
    border-radius: 3px;
}

/* button styling */

.jumbotron-form-money input[type=submit] {
    background-color: rgb(221,19,123);
    color: rgba(255,255,255); 
    border: none;
    border-radius: 5%;
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}

.jumbotron-form-money input[type=submit]:hover {
    background-color: rgb(255,255,255);
    color: rgb(221,19,123);
}

