    background-size: cover;
}

/* animated down arrow */

.arrow-container {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%; 
}

.arrow-background {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 3rem;
    height: 3rem;
    padding: .3rem;
    border-radius: 50%;
    background-color: rgb(255,255,255);
    box-shadow:  0 2px 5px 0 rgba(0,0,0,.18), 0 2px 7px 0 rgba(0,0,0,.15);
    z-index: 100;
}

.arrow {
    position: relative;    
    -webkit-animation: 1s linear infinite arrow;    
            animation: 1s linear infinite arrow;    
    color: rgb(221,19,123);  
    width: 2rem;
    height: 2rem;  
}

@-webkit-keyframes arrow {
    from {
        top: 0;
        opacity: 0;
    } 
    to {
        top: 25%;
        opacity: .8;
    }    
}

@keyframes arrow {
    from {
        top: 0;
        opacity: 0;
    } 
    to {
        top: 25%;
        opacity: .8;
    }    
}
